nav {
  width: auto;
  height: fit-content;
  padding: 7px 100px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: space-between;
  background-color: white;
}

#logo {
  width: 110px;
  height: 60px;
}

.sidenav {
  width: auto;
  height: fit-content;
}

.sidenav button {
  padding: 6px 25px;
  margin: 4px 10px 0;
  cursor: pointer;
  color: #ea0000;
  font-weight: bold;
  border: 1px solid #ed0000;
  box-sizing: border-box;
  border-radius: 3px;
  background-color: transparent;
}

#logout,
.sidenav button:nth-of-type(2) {
  color: whitesmoke;
  border: none;
  background-color: #ea0000;
}

.sidenav button:nth-of-type(1):hover {
  background-color: #ea000017;
}

.auth_menu {
  text-align: center;
  background-color: white;
}

.auth_menu section {
  padding: 5px 0;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-evenly;
}

.auth_menu .item {
  font-size: 15px;
  font-weight: 600;
  text-decoration: none;
  color: #525252;
}

.auth_menu .item:hover {
  color: #c6414c;
}
